import * as React from 'react';
import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Controols from '../components/Controols';
import ShippingDetail from '../components/ShippingDetail';
import OrderNumber from '../components/OrderNumber';
import Profile from '../components/Profile';
import Avatar from '../components/Avatar';
import TabsInfo from '../components/TabsInfo';
import ComingSoon from '../components/ComingSoon';


function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

export default function TabsContainer() {
  const [value, setValue] = React.useState(0);
  const [controller, setController] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Box  sx={{ width: '100%' }}>
      <Box className='simpleTab' sx={{ borderBottom: 1,borderTop: 1, borderColor: 'white',paddingTop:'10px',paddingBottom:'10px' }}>
        <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
          <Tab label="Console" {...a11yProps(0)} />
          <Tab label="Controllers" {...a11yProps(1)} />
          <Tab label="Accessories" {...a11yProps(2)} />
          <Tab label="Gaming Chairs" {...a11yProps(3)} />
          <Tab label="Monitors" {...a11yProps(4)} />
          <Tab label="VC" {...a11yProps(5)} />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0}>
        {/* <TabsInfo /> */}
        <ComingSoon />
      </TabPanel>
      <TabPanel value={value} index={1}>
        {controller===0 && <Controols setTab={setValue} cTab={setController} />}
        {controller===1 && <ShippingDetail setTab={setValue} cTab={setController} />}
        {controller===2 && <OrderNumber setTab={setValue} cTab={setController} />}
        {/* <ComingSoon /> */}
      </TabPanel>
      <TabPanel value={value} index={2}>
        {/* <ShippingDetail setTab={setValue} /> */}
        <ComingSoon />
      </TabPanel>
      <TabPanel value={value} index={3}>
        {/* <OrderNumber /> */}
        <ComingSoon />
      </TabPanel>
      <TabPanel value={value} index={4}>
        {/* <Profile /> */}
        <ComingSoon />
      </TabPanel>
      <TabPanel value={value} index={5}>
        {/* <Avatar /> */}
        <ComingSoon />
      </TabPanel>
    </Box>
  );
}
