import * as React from 'react';
import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Controols from '../components/Controols';
import ShippingDetail from '../components/ShippingDetail';
import OrderNumber from '../components/OrderNumber';
import Profile from '../components/Profile';
import Avatar from '../components/Avatar';
import TabsInfo from '../components/TabsInfo';
import {useNavigate} from "react-router-dom";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

export default function ColoredTabContainer() {
  let navigate = useNavigate()
  const [value, setValue] = React.useState(4);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Box  sx={{ width: '100%' }}>
      <Box className='coloredTab' sx={{ borderBottom: 1,borderTop: 1, borderColor: 'white',paddingTop:'10px',paddingBottom:'10px' }}>
        <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
          <Tab className='firstTab' label="EdIT Profile" {...a11yProps(0)} />
          <Tab className='SecondTab' onClick={()=>navigate(`/wheel`, { replace: true })} label="THE WHEEL" {...a11yProps(1)} />
          <Tab className='thirdTab' onClick={()=>navigate(`/shop`, { replace: true })} label="THE SHOP" {...a11yProps(2)} />
          <Tab className='forthTab' label="RPM AVATAR" {...a11yProps(3)} />
          <Tab className='fifthTab' label="FAQ" {...a11yProps(4)} />
          <Tab className='SisthTab' onClick={()=>navigate(`/`, { replace: true })} label="Main Page" {...a11yProps(5)} />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0}>
        <Profile />
      </TabPanel>
      <TabPanel value={value} index={1}>
        {/* <Controols /> */}
      </TabPanel>
      <TabPanel value={value} index={2}>
        {/* <ShippingDetail /> */}
      </TabPanel>
      <TabPanel value={value} index={3}>
        <Avatar />
      </TabPanel>
      <TabPanel value={value} index={4}>
        {/* <TabsInfo /> */}
      </TabPanel>
      <TabPanel value={value} index={5}>
        {/* <OrderNumber /> */}
      </TabPanel>
    </Box>
  );
}
