import React,{useEffect} from 'react'
import Grid from '@mui/material/Grid';
import profile from '../images/profile.png'
import sIcon1 from '../images/sIcon1.png'
import sIcon2 from '../images/sIcon2.png'
import sIcon3 from '../images/sIcon3.png'
import Button from 'react-bootstrap/Button'
import {useNavigate} from "react-router-dom";
import {useMediaQuery} from '@mui/material';
import { useTheme } from '@mui/material/styles';


function NewHeader({color}) {
  let navigate = useNavigate();
  const theme = useTheme();
  const largeScreen = useMediaQuery(theme => theme.breakpoints.up('md'));
  const smallScreen = useMediaQuery(theme => theme.breakpoints.down('sm'));
  
  useEffect(() => {
    anchorClicked(document.location.pathname)
  }, [])
  

  const anchorClicked = (id) =>{
    var elements = document.getElementsByClassName('newNavBtn');
    for(var i=0;i<elements.length;i++){
      elements[i].classList.remove("active");
    }
    document.getElementById(id).classList.add("active");
    if(id === '/' ){  navigate(`/`); }
    if(id === '/newRoadmap' ){ navigate(`/newRoadmap`); } 
    if(id === '/newHud' ){ navigate(`/newHud`); }  
    if(id === '/newrewards' ){ navigate(`/newrewards`); } 
    if(id === '/newabout' ){ navigate(`/newabout`); } 
    if(id === '/newfaqs' ){ navigate(`/newfaqs`); } 
    if(id === '/newteam' ){ navigate(`/newteam`); } 
    
      
    // if(id === 'ROADMAP' ){ navigate(`/roadmap`, { replace: true }); }
    // if(id === 'TEAM' ){ navigate(`/team`, { replace: true }); }
    // if(id === 'FAQS' ){ navigate(`/FAQS`, { replace: true }); }
  }
  return (
    <Grid container>
        { largeScreen? 
           <Grid container justifyContent={largeScreen?'space-between':'center'}>
            <Grid item xs={12} md={2} >
               <Grid p={3} pb={0} container justifyContent={'center'} alignItems={'center'}>
                    <img src={profile} alt='img' className='w-100 headImg' />
               </Grid>
            </Grid>
            <Grid item xs={12} md={8} px={largeScreen?5:0} >
              <div className='navContainer' style={{backgroundColor:color}}>
                  <div className='innerNavC'>
                    <div style={{display:'flex'}}>
                    <Button id='/'  className='newNavBtn newNavBtnUB w-100 home'
                    onClick={()=>anchorClicked('/')} >Home</Button>
                    <Button id='/newRoadmap' className='newNavBtn newNavBtnUB w-100 roadmap'
                    onClick={()=>anchorClicked('/newRoadmap')}>ROADMAP</Button>
                    <Button id='/newabout'  className='newNavBtn newNavBtnUB w-100 about'
                    onClick={()=>anchorClicked('/newabout')}>ABOUT</Button>
                    <Button id='/newfaqs'  className='newNavBtn newNavBtnUB w-100 faqs' 
                    onClick={()=>anchorClicked('/newfaqs')} >FAQs</Button>
                  </div>
                  <div style={{display:'flex'}}>
                    <Button id='/newHud'  className='newNavBtn newNavBtnBB w-100 hud'
                    onClick={()=>anchorClicked('/newHud')}>HUD</Button>
                    <Button id='/newrewards'  className='newNavBtn newNavBtnBB w-100 rewards'
                    onClick={()=>anchorClicked('/newrewards')}>REWARDS</Button>
                    <Button id='/newteam'  className='newNavBtn newNavBtnBB w-100 team'
                    onClick={()=>anchorClicked('/newteam')}>TEAM</Button>
                    <Button id='docs'  className='newNavBtn newNavBtnBB w-100 docs'
                    onClick={()=>anchorClicked('docs')}>DOCs</Button>
                  </div>
                  </div>
              </div>
            </Grid>
            <Grid item xs={12} md={2} p={1} >
               <Button  className='nWalletBtn w-100' >Connect	wallet</Button>
               <Grid container display={'flex'} py={1} justifyContent={'space-between'} alignItems={'end'}>
                 <Grid item xs={4} p={1} >
                    <img src={sIcon1} alt='img' className='w-100' />
                 </Grid>
                 <Grid item xs={4} p={1} >
                    <img src={sIcon2} alt='img' className='w-100' />
                 </Grid>
                 <Grid item xs={4} p={1} >
                    <img src={sIcon3} alt='img'  className='w-100' />
                 </Grid>
               </Grid>
            </Grid>
           </Grid>
        :  <Grid container justifyContent={largeScreen?'space-between':'center'}>
            
            <Grid item xs={12} md={8} px={largeScreen?5:0} >
              <div className='navContainer' style={{backgroundColor:color}}>
                  <div className='innerNavC'>
                    <div style={{display:'flex'}}>
                    <Button id='/'  className='newNavBtn newNavBtnUB w-100 home'
                    onClick={()=>anchorClicked('/')} >Home</Button>
                    <Button id='/newRoadmap' className='newNavBtn newNavBtnUB w-100 roadmap'
                    onClick={()=>anchorClicked('/newRoadmap')}>ROADMAP</Button>
                    <Button id='/newabout'  className='newNavBtn newNavBtnUB w-100 about'
                    onClick={()=>anchorClicked('/newabout')}>ABOUT</Button>
                    <Button id='/newfaqs'  className='newNavBtn newNavBtnUB w-100 faqs' 
                    onClick={()=>anchorClicked('/newfaqs')} >FAQs</Button>
                  </div>
                  <div style={{display:'flex'}}>
                    <Button id='/newHud'  className='newNavBtn newNavBtnBB w-100 hud'
                    onClick={()=>anchorClicked('/newHud')}>HUD</Button>
                    <Button id='/newrewards'  className='newNavBtn newNavBtnBB w-100 rewards'
                    onClick={()=>anchorClicked('/newrewards')}>REWARDS</Button>
                    <Button id='/newteam'  className='newNavBtn newNavBtnBB w-100 team'
                    onClick={()=>anchorClicked('/newteam')}>TEAM</Button>
                    <Button id='docs'  className='newNavBtn newNavBtnBB w-100 docs'
                    onClick={()=>anchorClicked('docs')}>DOCs</Button>
                  </div>
                  </div>
              </div>
            </Grid>
            <Grid item xs={12} md={2} >
               <Grid p={3} pb={0} container justifyContent={'center'} alignItems={'center'}>
                    <img src={profile} alt='img' className='w-100 headImg' />
               </Grid>
            </Grid>
            <Grid item xs={12} md={2} p={1} >
              <Grid  container justifyContent={'center'} alignItems={'center'}>
                <Grid item xs={smallScreen?6:4}>
                      <Grid  container  justifyContent={'center'} alignItems={'center'}>
                        
                      <Button  className='nWalletBtn w-100' >Connect	wallet</Button>
                      <Grid container display={'flex'} py={1} justifyContent={'space-between'} alignItems={'end'}>
                        <Grid item xs={4} p={1} >
                            <img src={sIcon1} alt='img' className='w-100' />
                        </Grid>
                        <Grid item xs={4} p={1} >
                            <img src={sIcon2} alt='img' className='w-100' />
                        </Grid>
                        <Grid item xs={4} p={1} >
                            <img src={sIcon3} alt='img'  className='w-100' />
                        </Grid>
                      </Grid>
                      </Grid>
                </Grid>
              </Grid>
            </Grid>
           </Grid>
           }
    </Grid>
  )
}

export default NewHeader