import logo from './logo.svg';
import './App.css';
import Header from './components/Header';
import NewHeader from './components/NewHeader';
import Mint from './screens/Mint';
import NewRoadmap from './screens/NewRoadmap';
import NewTeam from './screens/NewTeam';
import NewReward from './screens/NewReward';
import NewFaqs from './screens/NewFaqs';
import NewHud from './screens/NewHud';
import About from './screens/About';
import About1 from './screens/About1';
import About2 from './screens/About2';
import About3 from './screens/About3';
import Roadmap from './screens/Roadmap';
import Shop from './screens/Shop';
import TenYearRoadMap from './screens/TenYearRoadMap';
import Team from './screens/Team';
import Wheel from './screens/Wheel';
import FAQS from './screens/FAQS';
import End from './screens/End';
import Coming from './screens/Coming';
import ProfileScreen from './screens/ProfileScren';
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import { useEffect, useState } from "react";
import { ThemeProvider } from "@mui/material/styles";
import theme from "./Theme";

function App() {
  const mintColor = '#3eb969'
  const aboutColor = '#38b6ff'
  const roadMapColor = '#ff914d'
  const teamColor = '#ff1616'
  const faqsColor = '#ff66c4'
  const hudColor = '#ffde59'
  const rewardColor = '#CB6CE6'
  const [color, setColor] = useState('')
  const [state, setstate] = useState('')
  const [header, setHeader] = useState(<Header />)

  // let header = <Header />
  const route = () =>{
    if(document.location.pathname === '/tenyearroadmap' || document.location.pathname === '/End' 
       || document.location.pathname === '/coming' || document.location.pathname === '/shop' 
       || document.location.pathname === '/wheel' || document.location.pathname === '/profile'){
      //  header = <></>
       setHeader(<></>)
       setstate('notNav')
    }else{
      { document.location.pathname === '/' && setColor(mintColor) }
      { document.location.pathname === '/newRoadmap' && setColor(roadMapColor) }
      { document.location.pathname === '/newHud' && setColor(hudColor) }
      { document.location.pathname === '/newrewards' && setColor(rewardColor) }
      { document.location.pathname === '/newabout' && setColor(aboutColor) }
      { document.location.pathname === '/newfaqs' && setColor(faqsColor) }
      { document.location.pathname === '/newteam' && setColor(teamColor) }
      
      
      // header = <Header />
      
    }
  }

  useEffect(() => {
    route()
  }, [])

  useEffect(() => {
    setHeader(<NewHeader color={color} />)
    setstate('setNav')
  }, [color])

  return (
    <div className="App" onClick={() => route()} >
      <ThemeProvider theme={theme}>
          <Router>
          {header}
          {/* <NewHeader /> */}
          <Routes>
            <Route path = '/' exact={true} element = {<Mint color={color} />} />
            <Route path = '/newRoadmap'  element = {<NewRoadmap color={color} />} />
            <Route path = '/newHud'  element = {<NewHud color={color} />} />
            <Route path = '/newrewards'  element = {<NewReward color={color} />} />
            <Route path = '/newabout'  element = {<About1 color={color} />} />
            <Route path = '/newfaqs'  element = {<NewFaqs color={color} />} />
            <Route path = '/newteam'  element = {<NewTeam color={color} />} />
            

            <Route path = '/about'  element = {<About color={aboutColor} />} />
            <Route path = '/about2'  element = {<About2 color={aboutColor} />} />
            <Route path = '/about3'  element = {<About3 color={aboutColor} />} />
            <Route path = '/roadmap'  element = {<Roadmap color={roadMapColor} />} />
            <Route path = '/tenyearroadmap'  element = {<TenYearRoadMap />} />
            <Route path = '/team'  element = {<Team color={teamColor} />} />
            <Route path = '/FAQS'  element = {<FAQS color={faqsColor} />} />
            <Route path = '/End'  element = {<End />} />
            <Route path = '/coming'  element = {<Coming />} />
            <Route path = '/shop'  element = {<Shop />} />
            <Route path = '/wheel'  element = {<Wheel />} />
            <Route path = '/profile'  element = {<ProfileScreen />} />
          </Routes>
          </Router>
      </ThemeProvider>
    </div>
  );
}

export default App;
